p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

@keyframes products-card {
  0% {
    transform: translateY(40%);
  }

  100% {
    transform: translateY(0%);
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

body {
  font-family: DM Sans, sans-serif;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
}

@media screen and (min-width: 320px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 12;
  background: rgba(0, 0, 0, .2);
  justify-content: center;
  align-items: center;
  transition: opacity .25s, visibility .25s;
  display: flex;
  position: fixed;
  top: 0;
}

.buy-modal {
  width: 310px;
  height: 510px;
  background-color: #fff5f6;
  border-radius: 10px;
  padding: 20px;
  transition: transform .25s;
  position: absolute;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 480px) {
  .buy-modal {
    width: 440px;
    height: 500px;
    padding: 30px;
  }
}

@media screen and (min-width: 768px) {
  .buy-modal {
    width: 470px;
    height: 520px;
    padding: 40px;
  }
}

.franchise-modal {
  width: 310px;
  height: 400px;
  background-color: #fff5f6;
  border-radius: 10px;
  padding: 20px;
  transition: transform .25s;
  position: absolute;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 480px) {
