    width: 500px;
    height: 470px;
    padding: 40px;
  }
}

.modal-close-btn {
  fill: #d41443;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  align-items: center;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 5px;
  right: 0;
}

@media screen and (min-width: 480px) {
  .modal-close-btn {
    top: 10px;
    right: 10px;
  }
}

.input-close_btn:hover {
  border: 1px solid #d41443;
  border-radius: 50%;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal-title {
  text-align: center;
  letter-spacing: .03em;
  color: #d41443;
  margin-top: 12px;
  margin-bottom: 12px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.15;
}

@media screen and (min-width: 480px) {
  .modal-title {
    margin-top: 0;
  }
}

.modal-franchise-title {
  text-align: center;
  letter-spacing: .03em;
  color: #d41443;
  margin-top: 12px;
  margin-bottom: 12px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.15;
}

@media screen and (min-width: 480px) {
  .modal-franchise-title {
    margin-top: 20px;
  }
}

@media screen and (min-width: 768px) {
  .modal-franchise-title {
    margin-bottom: 30px;
  }
}

.modal-input-list {
  margin-bottom: 10px;
}

.modal-label {
  letter-spacing: .01em;
  color: #ffa5ba;
  margin-bottom: 4px;
  font-size: 12px;
  line-height: 1.16;
  display: block;
}

.modal-input-wrap {
  position: relative;
}

.modal-input {
  width: 100%;
  height: 40px;
  cursor: pointer;
  border: 1px solid rgba(255, 165, 186, .8);
  border-radius: 10px;
  outline: none;
  padding-left: 42px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-input:hover, .modal-input:focus {
  border-color: #d41443;
}

.input-icon {
  fill: #ffa5ba;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

.modal-input:focus + .input-icon, .modal-input:hover + .input-icon {
  fill: #d41443;
}

.modal-text {
  width: 100%;
  height: 120px;
  resize: none;
  border: 1px solid rgba(255, 165, 186, .8);
  border-radius: 10px;
  outline: none;
  padding: 12px 16px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-text:hover, .modal-text:focus {
  border-color: #d41443;
}

.modal-text::-ms-input-placeholder {
  letter-spacing: .01em;
  color: rgba(117, 117, 117, .5);
  font-size: 12px;
  line-height: 1.16;
}

.modal-text::placeholder {
  letter-spacing: .01em;
  color: rgba(117, 117, 117, .5);
  font-size: 12px;
  line-height: 1.16;
}

.product-selection {
  margin-bottom: 20px;
}

.product-selection_title {
  color: #ffa5ba;
  font-weight: 500;
}

.modal-radio-fields {
  color: #ffa5ba;
  justify-content: flex-start;
  align-items: center;
  margin-top: 10px;
  display: flex;
}

.modal-radio, .radio-label {
  cursor: pointer;
}

.modal-radio {
  height: 22px;
  width: 22px;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: none;
  position: relative;
}

.modal-radio:not(:first-child) {
  margin-left: 10px;
}

.modal-radio:before {
  content: "";
  width: 14px;
  height: 14px;
  background-color: #fff;
  border: 2px solid #d41443;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-radio:checked:after {
  content: "";
  width: 7px;
  height: 7px;
  visibility: visible;
  background-color: #d41443;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

